@font-face{font-family:"汉蒂灵飞经";src:local("汉蒂灵飞经"),url(../assets/fonts//lingfeijin.ttf)}.header-wrapper{background-color:#000;display:flex;flex-direction:row;justify-content:space-between;align-items:center;padding:0 96px;width:100%;height:60px;box-sizing:border-box;gap:72px}.header-wrapper .left-icon{flex-shrink:0;flex-grow:0;width:36px;aspect-ratio:1}.header-wrapper .left-icon svg{fill:#fff;transition:fill .3s ease-in-out;cursor:pointer}.header-wrapper .left-icon:hover svg{fill:#5f88f0;transition:fill .3s ease-in-out}.header-wrapper .right-panel{flex-grow:1;display:flex;flex-direction:row;align-items:center;justify-content:flex-end;height:100%}.header-wrapper .right-panel .nav-buttons{flex-grow:0;flex-basis:704px;display:flex;flex-direction:row;align-items:center;width:auto;height:100%;cursor:pointer}.header-wrapper .right-panel .nav-buttons .nav-button{flex-shrink:1;flex-grow:1;height:100%;min-width:80px;box-sizing:border-box;display:flex;background-color:rgba(0,0,0,.308);flex-direction:column;align-items:center;justify-content:center;gap:8px}.header-wrapper .right-panel .nav-buttons .nav-button .text{font-size:20px;color:#fff}.header-wrapper .right-panel .nav-buttons .nav-button .temp{width:100%}.header-wrapper .right-panel .nav-buttons .nav-button .temp .line{height:3px;border-radius:3px;width:0px;background:#fefcd5;transition:width .5s ease-in-out;transform:translate3d(16px, 0, 0)}.header-wrapper .right-panel .nav-buttons .nav-button:hover .temp .line{width:calc(100% - 32px);transition:width .5s ease-in-out}.header-wrapper .right-panel .nav-buttons .hamburger{display:none}@media screen and (max-width: 768px){.header-wrapper .right-panel .nav-buttons .nav-button{display:none}.header-wrapper .right-panel .nav-buttons .hamburger{flex-grow:1;opacity:1;display:flex;flex-direction:row-reverse}}